<template>
  <el-form class="user" ref="userForm" :model="user" label-width="80px">
    <el-form-item label="用户头像 :">
      <div>
        <div v-if="url" class="user__image" @click="chooseFile">
          <img :src="url">
        </div>
        <el-button v-else @click="chooseFile" size="mini">图片上传</el-button>
        <input
          hidden
          type="file"
          ref="fileInput"
          @change="fileChangeHandle($event)"
        />
      </div>
    </el-form-item>
    <el-form-item label="用户名 :">
      <el-input size="mini" class="user__input" v-model="user.name"></el-input>
    </el-form-item>
    <el-form-item label="电话 :">
      <el-input size="mini" class="user__input" v-model="user.phone"></el-input>
    </el-form-item>
    <el-form-item label="邮箱 :">
      <el-input size="mini" class="user__input" v-model="user.email"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button size="mini" type="primary" @click="onSubmit">立即修改</el-button>
      <el-button size="mini">取消</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { userUpdate } from "@/api/common.js";
import upload from "@/api/upload.js";

import url from "@/assets/img/logo.png";

export default {
  name: "User",
  data() {
    return {
      user: {
        name: "",
        email: "",
        phone: "",
        avatar_id: 1
      },
      url
    }
  },
  methods: {
    onSubmit() {
      userUpdate(this.user).then(() => {
        this.$message.success("修改成功!");
        Object.keys(this.user).forEach(i => {
          this.user[i] = "";
        })
        this.url = "";
      }).catch(data => {
        this.$message.error(data.response.data.message);
      })
    },
    chooseFile() {
      this.$refs.fileInput.click();
    },
    fileChangeHandle(evt) {
      const files = evt.target.files || evt.dataTransfer.files;
      Promise.all([...files].map(file => upload(file))).then(() => {
        // const { file_id, file_url, filename } = data[0].data;
        // this.user.avatar_id = file_id;
      });
    }
  }
}
</script>

<style lang="scss">
$prefix: user;

.#{$prefix} {
  &__input {
    width: 300px;
  }
  &__image {
    width: 80px;
    height: 80px;
    cursor: pointer;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;      
    }
  }
}

</style>
